import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import List from '@/components/List'
import Detail from '@/components/Detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      meta:{index:0}
    },
    {
      path: '/List',
      name: 'List',
      component: List,
      meta:{index:1}
    }
    ,
    {
      path: '/Detail/:Id',
      name: 'Detail',
      component: Detail,
      meta:{index:2}
    }
  ]
})



