<template>
  <div class="detail">
    <h3>{{detail.title}}</h3>
    <div class="content" v-html="removeHtmlStyle(detail.content)"></div>
    <!--<div class="text-center">-->
        <!--<Button type="default" @click="$router.back(-1)" >返回</Button>-->
    <!--</div>-->
    <Nav></Nav>
  </div>
</template>

<script>
  import axios from 'axios'
  import Nav from '@/components/Nav.vue'
  export default {
    name: 'Index',
    components:{
      Nav
    },
    data() {
      return {
        detail:''
      }
    },
    mounted(){
      axios.get('/api/info/queryMeetingById/'+this.$route.params.Id+'.do').then( res => {
        this.detail = res.data;
        // this.detail.content = this.detail.content.replace(/style\s*?=\s*?([‘"])[\s\S]*?\1/,'');
        // console.log(this.detail.content)
      })
    }
  }
</script>

<style lang="scss">
  .detail{
    padding-bottom: 52px;
    padding-top: 30px;
    color: #222222;
    width: 100%;
    height: 100%;

  }
  h3 {
    margin-bottom: 15px;
    font-size: 16px;
    text-align: center;
  }
  .content{
    font-size: 14px;
    margin-bottom: 20px;
    padding: 0 24px;
  }
  .text-center{
    text-align: center;
  }
  img{
    max-width: 100%;
    margin-top: 10px;
  }
  .content {
    font-size: 14px!important;
  }
  .MsoNormal{
    text-indent: 2em;
  }
</style>



