<template>
  <div class="list">
    <ol>
      <Scroll :on-reach-bottom="handleReachBottom" :loading-text="loadingText" height="100%">
          <li v-for="(item,index) in list" :key="index">
            <router-link :to="{name:'Detail',params:{Id:item.id}}">
              <div class="info">
                <!--<div class="icon-box">-->
                <!--<img src="@/assets/icon.png" width="38" height="38" alt="">-->
                <!--</div>-->
                <span class="name">{{item.title}}</span>
              </div>
              <span class="extra" v-html="dateChange(item.add_time)"></span>
            </router-link>
          </li>
      </Scroll>
    </ol>
    <Nav></Nav>
  </div>
</template>
<script>
  import axios from 'axios'
  import Nav from '@/components/Nav.vue'

  export default {
    components: {
      Nav
    },
    data() {
      return {
        list: [],
        page: 1,
        flag: false,
        loadingText:'加载中'
      }
    },
    methods: {
      handleReachBottom() {
        if(this.flag){
          this.loadingText = '没有更多内容'
        }
        return new Promise(resolve => {
          setTimeout(() => {
            axios.get('/api/info/queryMeetingByPage.do', {
              params: {
                rows: 10,
                page: this.page++
              }
            }).then(res => {
              if (!res.data.length) {
                this.flag = true
              }
              this.list = this.list.concat(res.data);
            })
            resolve();
          }, 1000);
        });
      }
    },
    mounted() {
      axios.get('/api/info/queryMeetingByPage.do', {
        params: {
          rows: 15,
          page: this.page
        }
      }).then(res => {
        this.list = res.data;
      })
    }
  }
</script>
<style lang="scss" scoped>
  ol {
    padding: 40px 20px;

  a {
    color: #222222;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  }
  li {

    line-height: 32px;
    font-size: 14px;
    margin-bottom: 15px;
    list-style-type: none;
  }

  .extra {
    float: right;
  }

  .text-center {
    text-align: center;
  }

  .icon-box {
    float: left;
    width: 59px;
    height: 59px;
    line-height: 59px;
    text-align: center;
    background: #f3f9ff;
    border-radius: 100%;
    margin-right: 14px;

  img {
    vertical-align: middle;
  }

  }
  .name {
    font-size: 18px;
    display: block;
    overflow: hidden;
  }

  .scan-btn {
    border-color: #979797;
    color: #37ac9b;
    width: 76px;
    height: 32px;
    border-radius: 4px;
    font-size: 18px;
    padding: 0;
    float: right;
  }
  .list{
    width: 100%;
    height: 100%;
  }

  .info {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
</style>



